import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'
import 'html5-reset/assets/css/reset.css'

import myTestDetail from '@/view/crowdTest/my-test-detail.vue'; //众测体验


Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'myTestDetail',
    component: myTestDetail
  }]
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



